"use client"

import { useState } from "react"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Search, Plus, Edit, Trash2, Phone, Mail, Calendar, History } from "lucide-react"
import { useToast } from "@/hooks/use-toast"

interface Client {
  id: string
  name: string
  email: string
  phone: string
  totalVisits: number
  lastVisit: string
  totalSpent: number
  avatar?: string
}

const mockClients: Client[] = [
  {
    id: "1",
    name: "Marie Dubois",
    email: "marie.d@email.com",
    phone: "06 12 34 56 78",
    totalVisits: 12,
    lastVisit: "2024-01-15",
    totalSpent: 780,
  },
  {
    id: "2",
    name: "Sophie Martin",
    email: "sophie.m@email.com",
    phone: "06 98 76 54 32",
    totalVisits: 8,
    lastVisit: "2024-01-10",
    totalSpent: 520,
  },
  {
    id: "3",
    name: "Amina Hassan",
    email: "amina.h@email.com",
    phone: "06 45 67 89 01",
    totalVisits: 15,
    lastVisit: "2024-01-18",
    totalSpent: 975,
  },
  {
    id: "4",
    name: "Fatou Diallo",
    email: "fatou.d@email.com",
    phone: "06 23 45 67 89",
    totalVisits: 20,
    lastVisit: "2024-01-20",
    totalSpent: 1300,
  },
]

export function ClientsTab() {
  const { toast } = useToast()
  const [clients, setClients] = useState<Client[]>(mockClients)
  const [searchTerm, setSearchTerm] = useState("")
  const [isAddDialogOpen, setIsAddDialogOpen] = useState(false)
  const [newClient, setNewClient] = useState({ name: "", email: "", phone: "" })

  const filteredClients = clients.filter(
    (c) =>
      c.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      c.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
      c.phone.includes(searchTerm),
  )

  const handleAddClient = () => {
    const client: Client = {
      id: Date.now().toString(),
      name: newClient.name,
      email: newClient.email,
      phone: newClient.phone,
      totalVisits: 0,
      lastVisit: "Jamais",
      totalSpent: 0,
    }
    setClients([...clients, client])
    toast({ title: "Client ajouté", description: `${client.name} a été ajouté avec succès` })
    setNewClient({ name: "", email: "", phone: "" })
    setIsAddDialogOpen(false)
  }

  const handleDeleteClient = (id: string) => {
    setClients(clients.filter((c) => c.id !== id))
    toast({ title: "Client supprimé" })
  }

  const getClientBadge = (visits: number) => {
    if (visits >= 15) return <Badge className="bg-yellow-500">VIP</Badge>
    if (visits >= 10) return <Badge className="bg-blue-500">Fidèle</Badge>
    return <Badge variant="outline">Nouveau</Badge>
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h2 className="text-3xl font-bold">Gestion des Clients</h2>
          <p className="text-muted-foreground">{clients.length} clients enregistrés</p>
        </div>
        <Dialog open={isAddDialogOpen} onOpenChange={setIsAddDialogOpen}>
          <DialogTrigger asChild>
            <Button className="bg-cyan-500 hover:bg-cyan-600">
              <Plus className="w-4 h-4 mr-2" />
              Nouveau client
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Enregistrer un nouveau client</DialogTitle>
              <DialogDescription>Ajoutez les informations du client</DialogDescription>
            </DialogHeader>
            <div className="space-y-4">
              <div>
                <Label>Nom complet</Label>
                <Input
                  value={newClient.name}
                  onChange={(e) => setNewClient({ ...newClient, name: e.target.value })}
                  placeholder="Marie Dubois"
                />
              </div>
              <div>
                <Label>Email</Label>
                <Input
                  type="email"
                  value={newClient.email}
                  onChange={(e) => setNewClient({ ...newClient, email: e.target.value })}
                  placeholder="marie@email.com"
                />
              </div>
              <div>
                <Label>Téléphone</Label>
                <Input
                  value={newClient.phone}
                  onChange={(e) => setNewClient({ ...newClient, phone: e.target.value })}
                  placeholder="06 12 34 56 78"
                />
              </div>
            </div>
            <DialogFooter>
              <Button variant="outline" onClick={() => setIsAddDialogOpen(false)}>
                Annuler
              </Button>
              <Button onClick={handleAddClient} className="bg-cyan-500 hover:bg-cyan-600">
                Enregistrer
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      </div>

      <div className="relative">
        <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-muted-foreground" />
        <Input
          placeholder="Rechercher un client par nom, email ou téléphone..."
          className="pl-10"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
      </div>

      <div className="grid gap-4">
        {filteredClients.map((client) => (
          <Card key={client.id}>
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-4">
                  <Avatar className="w-12 h-12">
                    <AvatarImage src={client.avatar || "/placeholder.svg"} />
                    <AvatarFallback>{client.name[0]}</AvatarFallback>
                  </Avatar>
                  <div>
                    <div className="flex items-center gap-2">
                      <h3 className="font-bold text-lg">{client.name}</h3>
                      {getClientBadge(client.totalVisits)}
                    </div>
                    <div className="flex gap-4 text-sm text-muted-foreground mt-1">
                      <span className="flex items-center gap-1">
                        <Mail className="w-4 h-4" />
                        {client.email}
                      </span>
                      <span className="flex items-center gap-1">
                        <Phone className="w-4 h-4" />
                        {client.phone}
                      </span>
                    </div>
                  </div>
                </div>

                <div className="flex items-center gap-6">
                  <div className="text-center">
                    <div className="flex items-center gap-1 text-muted-foreground text-sm">
                      <History className="w-4 h-4" />
                      Visites
                    </div>
                    <div className="text-2xl font-bold">{client.totalVisits}</div>
                  </div>
                  <div className="text-center">
                    <div className="flex items-center gap-1 text-muted-foreground text-sm">
                      <Calendar className="w-4 h-4" />
                      Dernière visite
                    </div>
                    <div className="text-sm font-medium">{client.lastVisit}</div>
                  </div>
                  <div className="text-center">
                    <div className="text-muted-foreground text-sm">Total dépensé</div>
                    <div className="text-2xl font-bold text-green-600">€{client.totalSpent}</div>
                  </div>

                  <div className="flex gap-2">
                    <Button variant="outline" size="icon">
                      <Edit className="w-4 h-4" />
                    </Button>
                    <Button variant="outline" size="icon" onClick={() => handleDeleteClient(client.id)}>
                      <Trash2 className="w-4 h-4 text-destructive" />
                    </Button>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  )
}
